import connect from '@vkontakte/vkui-connect-promise';

export const getUser = async () => {
	try{
		const response = await connect.send("VKWebAppGetUserInfo", {});;
		return await response.data;
	} catch(err) {
		throw Error(err);
	}
};

export const getAccessToken = async () => {
	try{
		const response = await connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status"});
		return await response.data.access_token;
	} catch (err) {
		throw Error(err);
	}
};

export const getFriends = async () => {
	try{
		const response = await connect.send("VKWebAppGetFriends", {});
		return await response.data.users;
	} catch (err) {
		throw Error(err);
	}
};